/* Start Events */
.events {
  padding-top: var(--main-padding-top);
  padding-bottom: var(--main-padding-bottom);
  position: relative;

  .container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    img {
      max-width: 450px;
    }

    .info {
      flex: 1;

      .time {
        display: flex;
        align-items: center;
        margin: 20px auto;
        gap: 10px;
        justify-content: center;

        .unit {
          border: 1px solid #d4d4d4;
          border-radius: 6px;
          text-align: center;
          width: 75px;
          transition: var(--main-transition);

          span {
            display: block;
            transition: var(--main-transition);

            &:first-child {
              font-size: 35px;
              padding: 15px;
              color: var(--main-color);
              font-weight: bold;
            }

            &:last-child {
              padding: 8px 10px;
              font-size: 13px;
              border-top: 1px solid #d4d4d4;
            }
          }

          &:hover,
          &:hover span:last-child {
            border-color: var(--main-color);
          }
        }
      }

      .title {
        text-align: center;
        margin: 40px 0 0;
        font-size: 30px;
      }

      .description {
        text-align: center;
        line-height: 1.7;
        font-size: 19px;
        color: #777;
      }
    }

    .subscribe {
      width: 100%;
      margin-top: 50px;

      form {
        width: 600px;
        margin: 20px auto;
        padding: 30px 40px;
        background-color: #f6f5f5;
        border-radius: 50px;
        display: flex;
        gap: 20px;

        input[type="email"] {
          padding: 20px;
          border-radius: 50px;
          border: none;
          flex: 1;
          caret-color: var(--main-color);

          &::placeholder {
            transition: opacity var(--main-transition);
          }

          &:focus {
            outline: none;
          }

          &:focus::placeholder {
            opacity: 0;
          }
        }

        input[type="submit"] {
          border-radius: 50px;
          border: none;
          background-color: var(--main-color);
          color: white;
          font-weight: bold;
          padding: 20px;
          cursor: pointer;
          transition: var(--main-transition);

          &:hover {
            background-color: var(--main-color-alt);
          }
        }
      }
    }
  }
}

@media (max-width: 991px) {
  .events img {
    display: none;
  }
  .dots-up,
  .dots-down {
    display: none;
  }
}

@media (max-width: 767px) {
  .events .subscribe form {
    max-width: 100%;
    padding: 20px;
    flex-direction: column;
    border-radius: 0;
  }

  .events .subscribe form input[type="email"] {
    border-radius: 0;
  }

  .events .subscribe form input[type="submit"] {
    border-radius: 0;
  }
}
/* End Events */
