/* Start Work Steps */
.work-steps {
  padding-top: var(--main-padding-top);
  padding-bottom: var(--main-padding-bottom);
  position: relative;
  background-color: var(--section-background);

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .image {
      max-width: 100%;
      margin-right: 100px;
    }

    .info {
      .box {
        background-color: #f6f5f5;
        padding: 30px;
        margin-bottom: 20px;
        border-radius: 6px;
        display: flex;
        align-items: center;
        border: 2px solid white;
        position: relative;
        z-index: 1;

        &::before {
          content: "";
          left: 50%;
          top: 50%;
          position: absolute;
          width: 0;
          height: 0;
          transform: translate(-50%, -50%);
          background-color: #ededed;
          z-index: -1;
          transition: var(--main-transition);
        }

        &:hover::before {
          width: 100%;
          height: 100%;
        }

        img {
          width: 64px;
          margin-right: 30px;
        }

        h3 {
          margin: 0;
          font-size: 22px;
        }

        p {
          color: #777;
          line-height: 1.7;
          margin: 10px 0 0;
          font-size: 18px;
        }
      }
    }
  }
}

@media (max-width: 991px) {
  .work-steps .container {
    flex-direction: column;
  }

  .work-steps .image {
    margin: 0 0 50px;
  }
}

@media (max-width: 767px) {
  .work-steps .info .box {
    flex-direction: column;
    text-align: center;
  }

  .work-steps .info .box img {
    margin: 0 0 30px;
  }
}
/* End Work Steps */
